  padding: 0 30px 60px 0;
}
.online-lesson__main-iframe {
  position: relative;
}
.online-lesson__main-iframe img {
  position: absolute;
  top: 29px;
  left: 20px;
}
.online-lesson__btn {
  max-width: 262px;
}
.online-lesson__btn-form {
  margin-top: 60px;
}
.online-lesson__title {
  color: #292C32;
  font-family: Montserrat;
  font-size: 20px;
  font-style: normal;
  font-weight: 700;
}
.online-lesson__info {
  display: flex;
  flex-direction: column;
  gap: 100px;
  padding-top: 30px;
}
.online-lesson__input {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.online-lesson__label {
  color: #292C32;
  font-family: Montserrat;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 24px;
  display: flex;
}
.online-lesson__label-time input {
  max-width: 90px;
}
.online-lesson__label input {
  border: 1px solid #f04973;
  height: 30px;
  border-radius: 10px;
  margin-left: 20px;
  padding-left: 20px;
}
.online-lesson__label textarea {
  border: 1px solid #f04973;
  border-radius: 10px;
  margin-left: 20px;
  padding: 20px;
  min-height: 150px;
}
.online-lesson__main-content {
  display: flex;
  justify-content: space-between;
  margin-top: 50px;
}
.online-lesson__information {
  border-radius: 10px;
  background: #FFF;
  box-shadow: 0px 10px 60px 0px rgba(42, 102, 193, 0.15);
  padding: 25px;
  width: 330px;
}
.online-lesson__information-title {
  color: #292C32;
  font-family: Montserrat;
  font-size: 15px;
  font-style: normal;
  font-weight: 700;
  line-height: 19px;
  padding-bottom: 15px;
}
.online-lesson__information-title span {
  color: #f04973;
  font-family: Montserrat;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 19px;
}
.online-lesson__information-descr {
  color: #292C32;
  font-family: Montserrat;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: 19px;
  padding-bottom: 10px;
}
.online-lesson__information-descr p {
  color: #f04973;
}
.online-lesson__information-timer {
  display: flex;
  flex-direction: column;
  gap: 6px;
  margin-bottom: 15px;
}
.online-lesson__information-timer-time {
  color: #8B919E;
  font-family: Montserrat;
  font-size: 13px;
  font-style: normal;
  font-weight: 500;
  line-height: 23px;
}
.online-lesson__information-timer-hour {
  color: #292C32;
  font-family: Montserrat;
  font-size: 20px;
  font-style: normal;
  font-weight: 700;
  line-height: 22px;
}
.online-lesson__information-link p {
  color: #292C32;
  font-family: Montserrat;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: 19px; /* 135.714% */
}
.online-lesson__information-link a {
  color: #f04973;
}
.online-lesson__information-btn {
  margin-top: 120px;
}
.online-lesson__banner {
  width: 100%;
  height: 300px;
  background-color: #f04973;
  position: absolute;
  top: 70px;
  z-index: 10;
  left: 0;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: all 0.5s ease;
}
.online-lesson__banner p {
  color: #292C32;
  text-align: center;
  font-family: EB Garamond;
  font-size: 50px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}
.online-lesson__banner-btn {
  width: 300px;
  margin-top: 50px;
}

@media (max-width: 1439px) {
  iframe {
    max-width: 800px;
    border-radius: 30px;
    border: 5px solid #f04973;
    max-height: 500px;
  }
}
.watermark {
  opacity: 0;
}

.leftwatermark {
  opacity: 0;
}

iframe .leftwatermark a {
  opacity: 0;
}

